import 'moment';
import 'bootstrap-daterangepicker';
import '!style-loader?insertAt=top!css-loader!node_modules/bootstrap-daterangepicker/daterangepicker.css';

export default class DateRangePicker {
  constructor(element, options = {}, locale = {}) {
    let localeOption = this.getLocalOptions(locale);
    options.locale = localeOption;
    console.log(options);
    $(element).daterangepicker(options);

  }

  getLocalOptions(locale) {
    let defaultLocale = this.getDefaultLocale();
    for (let argv in defaultLocale) {
      if(locale[argv]) {
        defaultLocale[argv] = locale[argv];
      }
    }
    return defaultLocale;
  }

  /**
     * yml
     */
  getDefaultLocale() {
    let locale_zh_CN = {
      'format': 'YYYY/MM/DD',
      'separator': '-',
      'applyLabel': '',
      'cancelLabel': '',
      'fromLabel': '',
      'toLabel': '',
      'customRangeLabel': '',
      'weekLabel': 'W',
      'daysOfWeek': [
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      'monthNames': [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      'firstDay': 1
    };

    let locale_en = {
      'format': 'yyyy/mm/dd',
      'separator': '-',
      'applyLabel': 'Apply',
      'cancelLabel': 'Cancel',
      'fromLabel': 'From',
      'toLabel': 'To',
      'customRangeLabel': 'Custom',
      'weekLabel': 'W',
      'daysOfWeek': [
        'Su',
        'Mo',
        'Tu',
        'We',
        'Th',
        'Fr',
        'Sa'
      ],
      'monthNames': [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December'
      ],
      'firstDay': 1
    };

    if (app.lang === 'zh_CN') {
      return locale_zh_CN;
    }

    return locale_en;

  }
}


